/* Even smaller */
/*GENERAL STYLES*/
html {
  font-size: 125%; }

div.App {
  position: relative; }

.wrapper {
  padding-bottom: 5%; }
  @media (max-width: 752px) {
    .wrapper {
      padding-bottom: 0; } }
  @media (max-width: 480px) {
    .wrapper {
      padding-top: 5%;
      padding-bottom: 0; } }
  @media (max-width: 320px) {
    .wrapper {
      padding-bottom: 0; } }

body {
  background-color: teal;
  text-align: center;
  font-size: 1rem;
  font-family: "Barlow", sans-serif; }
  @media (max-width: 480px) {
    body {
      font-size: 0.9rem; } }

/*HEADER STYLES*/
header {
  max-width: 100%; }
  header h1 {
    font-family: "Cormorant Garamond", serif;
    font-size: 6rem;
    font-weight: 700;
    color: lemonchiffon;
    text-shadow: 2px 2px black;
    margin: 2% auto; }
    @media (max-width: 752px) {
      header h1 {
        font-size: 2.5rem; } }
    @media (max-width: 480px) {
      header h1 {
        font-size: 2rem; } }
  header h2 {
    font-family: "Cormorant Garamond", serif;
    color: white;
    font-size: 1.8rem; }
    @media (max-width: 752px) {
      header h2 {
        font-size: 1.5rem; } }
    @media (max-width: 480px) {
      header h2 {
        font-size: 1.1rem; } }
    header h2 span {
      text-decoration-line: line-through;
      color: lemonchiffon; }

header p {
  color: lemonchiffon; }

.perfectSentence {
  background: black;
  color: white;
  width: 40%;
  text-align: center;
  margin: 0 auto;
  padding: 0; }
  .perfectSentence p {
    width: 100%;
    font-family: "Barlow", sans-serif;
    font-style: italic;
    margin: 2%; }

/* FORM STYLES */
div.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center; }
  div.form form {
    font-family: "Barlow", sans-serif;
    width: 70%;
    margin: 0 auto;
    text-align: center;
    max-width: 70%; }
    div.form form textarea {
      resize: none;
      width: 100%;
      border: 1px solid black;
      padding: 1%;
      margin-bottom: 2%; }

button {
  background: black;
  color: white;
  box-shadow: none;
  border: none;
  border-radius: 10px;
  font-family: "Barlow", sans-serif;
  padding: 1%;
  margin-bottom: 5%; }

button:focus {
  border: 1.5px solid white; }

button:hover {
  background: lemonchiffon;
  color: black; }

button:disabled {
  background: darkslategray;
  color: white;
  border: none;
  text-decoration: line-through; }

.resetButton {
  margin-top: 3%; }

/* BODY STYLES */
.wrongWordListItem {
  color: lemonchiffon; }

.errorHandling {
  color: lemonchiffon;
  font-size: 1rem;
  font-weight: 700;
  text-transform: uppercase; }

.errorMessage {
  color: white;
  font-weight: 700; }

ul.errorMessages {
  color: lemonchiffon;
  background: #757575;
  width: 50%;
  padding: 5%;
  margin: 0 auto 5%;
  text-align: center; }
  ul.errorMessages li {
    font-family: "Barlow", sans-serif;
    margin: 0;
    list-style: none; }
    ul.errorMessages li p.shortMessage {
      font-style: italic;
      margin: 0; }

.userInput {
  width: 50%;
  margin: 0 auto; }
  .userInput p.savedInput {
    font-style: italic;
    color: lemonchiffon;
    background: black;
    font-size: 1.5rem;
    width: 100%;
    padding: 2%; }

footer {
  width: 100%;
  display: block;
  text-align: center;
  font-size: 0.8rem;
  margin-top: 5%; }
  @media (max-width: 480px) {
    footer {
      font-size: 0.5rem; } }
  footer p {
    display: inline-block;
    padding: 0;
    color: white; }
